/* Vocabulary Tab Styles */
.vocabulary-tab {
  width: 100%;
}

/* Mode Selector */
.mode-selector {
  display: flex;
  gap: 6px;
  margin-bottom: 12px;
  background: #f5f5f5;
  padding: 3px;
  border-radius: 10px;
}

.mode-btn {
  flex: 1;
  padding: 8px;
  border: none;
  background: transparent;
  color: #666;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  transition: all 0.3s ease;
}

.mode-btn.active {
  background: white;
  color: #7c3aed;
  box-shadow: 0 2px 8px rgba(124, 58, 237, 0.2);
}

/* Controls */
.compact-controls {
  display: flex;
  gap: 8px;
  margin-bottom: 8px;
}

.compact-dropdown {
  flex: 1;
  padding: 6px 10px;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  font-size: 13px;
  background: white;
  cursor: pointer;
}

.action-controls {
  display: flex;
  gap: 8px;
  margin-bottom: 10px;
}

.compact-action-btn {
  flex: 1;
  padding: 8px;
  border: none;
  border-radius: 6px;
  background: linear-gradient(135deg, #7c3aed 0%, #a855f7 100%);
  color: white;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s;
}

.compact-action-btn:hover {
  transform: translateY(-1px);
}

/* Mini Stats */
.mini-stats {
  display: flex;
  justify-content: space-around;
  padding: 6px 8px;
  background: rgba(124, 58, 237, 0.1);
  border-radius: 6px;
  margin-bottom: 10px;
  font-size: 11px;
}

.mini-stat {
  color: #6b7280;
}

.mini-stat strong {
  color: #7c3aed;
  font-weight: 700;
  margin-left: 2px;
}

/* Vocabulary List */
.interactive-vocab-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: calc(100vh - 190px);
  overflow-y: auto;
  padding-bottom: 20px;
}

/* Interactive Word Card */
.interactive-word-card {
  display: grid;
  grid-template-columns: 1fr 200px;
  align-items: center;
  background: linear-gradient(135deg, #7c3aed 0%, #a855f7 100%);
  color: white;
  padding: 20px;
  border-radius: 12px;
  min-height: 160px;
  gap: 20px;
  box-shadow: 0 3px 10px rgba(124, 58, 237, 0.2);
  transition: all 0.3s;
  position: relative;
}

.interactive-word-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(124, 58, 237, 0.3);
}

/* Bookmark Star */
.bookmark-star {
  position: absolute;
  top: 6px;
  right: 6px;
  font-size: 18px;
  cursor: pointer;
  opacity: 0.5;
  transition: all 0.3s;
  z-index: 10;
}

.bookmark-star.active {
  opacity: 1;
  color: #fbbf24;
}

.bookmark-star:hover {
  opacity: 1;
  transform: scale(1.1);
}

/* Word Column */
.word-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 6px;
  text-align: left;
}

.japanese-word {
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 1.2;
  cursor: pointer;
}

.japanese-word ruby rt {
  font-size: 0.55em;
  opacity: 0.9;
}

.word-meaning {
  font-size: 1.1rem;
  opacity: 0.95;
  color: #f0f8ff;
  font-weight: 500;
  cursor: pointer;
}

/* Action Buttons */
.action-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 60px 60px;
  gap: 6px;
  width: 200px;
  justify-self: end;
}

.action-btn-small {
  border: 2px solid rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  min-height: 60px;
  padding: 8px;
}

.action-btn-small:active {
  transform: scale(0.95);
}

.action-btn-small.active {
  background: white;
  color: #7c3aed;
}

.action-btn-small:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.btn-know {
  background: rgba(34, 197, 94, 0.25);
  border-color: rgba(34, 197, 94, 0.5);
  grid-column: span 3;
}

.btn-reset {
  background: rgba(99, 102, 241, 0.25);
  border-color: rgba(99, 102, 241, 0.5);
  font-size: 20px;
}

/* Known Mode */
#known-mode {
  width: 100%;
}

.known-words-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
}

.known-word-card {
  background: linear-gradient(135deg, #22c55e 0%, #84cc16 100%);
  color: white;
  padding: 12px;
  border-radius: 10px;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(34, 197, 94, 0.2);
  transition: all 0.3s;
}

.known-word-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
}

.known-word-jp {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 4px;
}

.known-word-kr {
  font-size: 0.9rem;
  opacity: 0.95;
}

/* Empty State */
.empty-message {
  text-align: center;
  padding: 48px 24px;
  color: #9ca3af;
  font-size: 1rem;
}

/* Hidden State */
.hidden {
  display: none;
}